      <ul class="category">
        <li>
          <label for="">연도</label>
          <input type="number" min="1900" max="2099" step="1" value="2016" />
        </li>
        <li>
          <label for="">장르</label>
          <select name="" id="">
            <option v-for="(gerne, i) in gerne" :key="i">
              {{ gerne }}
            </option>
          </select>
        </li>
        <li>
          <label for="">국가</label>
          <select name="" id="">
            <option v-for="(country, i) in country" :key="i">
              {{ country }}
            </option>
          </select>
        </li>
      </ul>
      <div class="search-input">
        <input type="text" placeholder="영화제목을 입력하세요." />
