<script>
import { gerne, country } from "@/js/naverCode.js";

export default {
  data() {
    return {
      gerne,
      country,
    };
  },
  methods: {
    getMovieList() {
      const search = {
        query: "",
        display: 10,
        start: 1,
        gerne: "1",
        country: "KR",
        yearfrom: 1990,
      };
      const data = this.$axios
        .get("https://openapi.naver.com/v1/search/movie.json", {
          headers: {
            "X-Naver-Client-Id": `${process.env.VUE_APP_NAVER_KEY}`,
            "X-Naver-Client-Secret": `${process.env.VUE_APP_NAVER_CLIENT}`,
          },
          params: search,
        })
        .then((res) => {
          return res.data.rss;
        });
      console.log(data);
    },
  },
};
</script>

